import React, { Component } from "react";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";
import { connect } from "react-redux";
import { getsupplierList } from "../actions/supplierAction";
import Axios from "axios";
import { getUUID } from "../actions/fakturAction";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getTerimaSupplierTemp } from "../actions/terimaSupplierAction";
import { warning } from "../Alert";
import PrintNota from "../Stocking/Component/PrintNota";
// import { Link } from "react-router-dom";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../actions/LogicAction";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";

const server = process.env.REACT_APP_BACKEND_URL;
const serverPOST = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    tampungBarang: state.provinsi.terimaSupplierTemp,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    totalNW: state.provinsi.totalNW,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
  };
};

class TerimaSupplier extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "TerimaSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getTerimaSupplierTemp()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      tanggal_invoice: formatedDate,
      tanggal_barang: formatedDate,
      noBon: "",
      totalNW: 0,
      totalBruto: 0,
      totalPkg: 0,
      totalGross: 0,
      totalOngkir: 0,
      kodeSupplier: "",
      namaSupplier: "",
      keterangan: "",
      startingDate: formatedDate,
      tampungBarang: [],
      tampungBarangLocal: [],
      urutan: localStorage.getItem("urutan") || 0,
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
    this.props.dispatch(getTerimaSupplierTemp());
    this.getSumData();
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    localStorage.setItem([input], e.target.value);
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
    localStorage.setItem("namaSupplier", result_explode[0]);
    localStorage.setItem("kodeSupplier", result_explode[1]);
  };

  handleSubmit(data) {
    var barang = JSON.parse(localStorage.getItem("TerimaSupplier")) || [];
    var jenis = data.kodeJenis ? data.kodeJenis : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    let isi = {
      noFaktur: this.props.noFaktur.toUpperCase(), //
      nettoDiscGlobalExt: 0,
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      qty: 0,
      k: parseFloat(data.k),
      rp: parseFloat(data.rp), //ongkos
      kadarCetak: result_explode[2],
      keterangan: data.keterangan.toUpperCase(),
      discGlobalPr: 0,
      kodeKategori: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
      kodeJenis: result_explode[0], //kodebarang
      noBarcode: "-",
      p: parseFloat(data.p),
      discGlobalGr: 0,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
      nettoDiscGlobal: 0,
    };
    barang.push(isi);
    localStorage.setItem("TerimaSupplier", JSON.stringify(barang));
    localStorage.setItem(
      "totalNW",
      barang
        .map((list) => list.netto)
        .reduce((a, b) => a + b, 0)
        .toFixed(3)
    );
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Transaksi Berhasil",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("TerimaSupplier")))
      .then(() => this.getSumData())
      .then(() => this.props.dispatch(getTerimaSupplierTemp()));
  }
  getSumData() {
    this.props.dispatch(getNetto("TerimaSupplier"));
    this.props.dispatch(getBruto("TerimaSupplier"));
    this.props.dispatch(getRp("TerimaSupplier"));
    this.props.dispatch(getPkg("TerimaSupplier"));
    this.props.dispatch(getQty("TerimaSupplier"));
  }
  kirimData() {
    var jenis = this.state.kodeSupplier
      ? this.state.kodeSupplier
      : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    if (this.state.kodeSupplier === "") {
      warning("Mohon pilih Supplier");
      return false;
    } else if (this.state.keterangan === "") {
      warning("Mohon isi Keterangan");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon Isi nomor Bon");
      return false;
    } else if (localStorage.getItem("TerimaSupplier") === null) {
      warning("Barang masih kosong, mohon tambahkan barang");
      return false;
    } else if (this.state.tanggalBarang === "") {
      warning("Mohon isi Tanggal");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal Invoice");
      return false;
    }
    var simpan = {
      noBon: this.props.noFaktur.toUpperCase(),
      namaSupplier: result_explode[1],
      kodeSupplier: result_explode[0],
      tanggalBarang: this.state.tanggalBarang,
      noFaktur: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal,
      barangDetails: JSON.parse(localStorage.getItem("TerimaSupplier")),
      footerItem: {
        totalNettoExt: localStorage.getItem("totalNW"),
        totalBruto: this.props.sumBruto,
        totalNetto: this.props.sumNetto,
        totalQty: 0,
        totalP: 0,
        totalK: 0,
        totalpkg: this.props.sumPkg,
        totalGross: 0,
        totalRp: this.props.sumRp,
      },
      footerDocument: {
        inputBy: this.props.username,
      },
    };
    console.log(JSON.stringify(simpan));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("TerimaSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kodeJenis,
        ticket.kadarCetak,
        ticket.p,
        ticket.k,
        ticket.pkg,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
        parseFloat(ticket.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      table.map((list) => list.pkg).reduce((a, b) => a + b, 0),
      table.map((list) => list.bruto).reduce((a, b) => a + b, 0),
      "",
      table
        .map((list) => list.netto)
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => list.rp)
        .reduce((a, b) => a + b, 0)
        .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak (%)",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverPOST + "/spp/dc/trm", simpan)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(getTerimaSupplierTemp()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "no Terima",
              this.props.noFaktur.toUpperCase(),
              "Tanggal",
              this.state.tanggal_invoice,
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Terima Barang Supplier",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("TerimaSupplier"))
          .then(() => localStorage.removeItem("noBon"))
          .then(() => localStorage.removeItem("tanggal"))
          .then(() => localStorage.removeItem("tanggalBarang"))
          .then(() => localStorage.removeItem("namaSupplier"))
          .then(() => localStorage.removeItem("kodeSupplier"))
          .then(() => localStorage.removeItem("keterangan"))
          .then(() => localStorage.removeItem("totalNW"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            this.setState({
              noBon: "",
              keterangan: "",
              totalNW: "",
              kodeSupplier: "",
            })
          )
          .then(() => this.props.dispatch(getTerimaSupplierTemp()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-9">
                      <h1>Terima Barang Supplier</h1>
                    </div>
                    {/* <div className="col-lg-3">
                      <div className="text-right">
                        <Link to="/ayugold.com/EditForm/terimaSupplier">
                          <button
                            className="btn btn-warning"

                            // data-target="#editModal"
                            // data-toggle="modal"
                            // data-backdrop="static"
                            // data-keyboard="false"
                          >
                            Edit Terima Barang
                          </button>
                        </Link>
                      </div>
                    </div> */}
                    <div className="col-lg-12">
                      <br />
                    </div>
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="kodeSupplier"
                        className="form-control"
                        onChange={this.pilihSupplier("kodeSupplier")}
                        // value={this.state.kodeSupplier}
                      >
                        <option value="DEFAULT | DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        {this.state.listSupplier.map((kode) => (
                          <option
                            value={kode.kodeSupplier + "|" + kode.namaSupplier}
                          >
                            {kode.namaSupplier}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Invoice</label>
                      <input
                        type="date"
                        id="tanggal_invoice"
                        name="tanggal_invoice"
                        className="form-control"
                        value={
                          localStorage.getItem("tanggal")
                            ? localStorage.getItem("tanggal")
                            : this.state.startingDate
                        }
                        onChange={this.inputChange("tanggal")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Barang</label>
                      <input
                        type="date"
                        id="tanggal_barang"
                        name="tanggal_barang"
                        className="form-control"
                        value={
                          localStorage.getItem("tanggalBarang")
                            ? localStorage.getItem("tanggalBarang")
                            : this.state.startingDate
                        }
                        onChange={this.inputChange("tanggalBarang")}
                      />
                    </div>
                    <div className="col-md-6 col-lg-4 mt-3">
                      <label htmlFor="">Keterangan</label>
                      <input
                        type="text"
                        id="keterangan"
                        name="keterangan"
                        className="form-control"
                        onChange={this.inputChange("keterangan")}
                        value={localStorage.getItem("keterangan")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 mt-3">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="noBon"
                        name="noBon"
                        className="form-control"
                        onChange={this.inputChange("noBon")}
                        value={localStorage.getItem("noBon")}
                      />
                    </div>
                    {this.props.totalNW >= 0 ? (
                      <div className="col-md-4 col-lg-4 mt-3">
                        <label htmlFor="">Total NW Ext</label>
                        <input
                          type="number"
                          step={0.001}
                          min={0}
                          id="totalNW"
                          name="totalNW"
                          className="form-control"
                          onChange={this.inputChange("totalNW")}
                          defaultValue={localStorage.getItem("totalNW")}
                        />
                      </div>
                    ) : (
                      <div className="col-md-4 col-lg-4 mt-3">
                        <label htmlFor="">Total NW Ext</label>
                        <input
                          type="number"
                          id="totalNW"
                          name="totalNW"
                          className="form-control"
                          onChange={this.inputChange("totalNW")}
                          value={localStorage.getItem("totalNW")}
                        />
                      </div>
                    )}
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6 col-md-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.kirimData()}
                            >
                              Simpan
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              data-target="#primary-header-modal"
                            >
                              Tambah Data
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {this.props.tampungBarang ? (
                    <ToolkitProvider
                      keyField="kodeJenis"
                      data={this.props.tampungBarang}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group mt-2">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <BootstrapTable
                      keyField="kodeJenis"
                      data={[]}
                      columns={this.state.columns}
                      bordered={true}
                      headerClasses="header-table"
                      noDataIndication="Tidak Ada Data"
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Terima Supplier
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier
                  from="terima"
                  onSubmit={(data) => this.handleSubmit(data)}
                  noFaktur={this.props.noFaktur}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TerimaSupplier);
